import {uniRate} from '../../components/uni-rate/uni-rate.vue'

export default {
	data() {
		return {
			title: 'Hello',
			footList: [{
					title: '早餐',
					list: ['鸡肉', '鸭肉', '鹅肉', '鸡蛋']
				},
				{
					title: '午餐',
					list: ['盖饭', '手扒饭', '咖喱饭', '鸡排饭']
				},
				{
					title: '晚餐',
					list: ['辣子鸡丁', '红烧肉', '回锅肉', '水煮肉片']
				},
				{
					title: '夜宵',
					list: ['烤茄子', '考韭菜', '烤小鱼', '烤生蚝']
				}
			],
			cp: 'cp',
			cound: 'cp0',
			coundIndex: 0,
			topArr: [],
			
		}
	},
	components: {
		uniRate
	},
	// 计算属性
	computed: {
		number(){
			return this.$store.state.count
		},
		priceAll(){
			return this.$store.getters.getCount
		},
		
	},
	onLoad(){
		this.getListTop()
	},
	methods: {
		addCount(){
			this.$store.commit('addNum')
		},
		
		getcound(index){
			this.coundIndex = index
			this.cound = `cp${index}`
			console.log(this.coundIndex)
		},
		// 滚动到顶部
		scrolltolower(){
			setTimeout(()=>{
				this.coundIndex = 3
			}, 80);
		},
		// 滚动触发
		scrollTop(e){
			let scrollTop = e.target.scrollTop;
			for(let i=0;i<this.topArr.length;i++){
				let a = this.topArr[i]
				let b = this.topArr[i+1]
				if(scrollTop>=a && scrollTop<=b){
					this.coundIndex = i
				}
			}
		},
		// 获取到标签到顶部的高度
		getListTop (){
			let topArr = []
			const query = uni.createSelectorQuery().in(this)
			query.selectAll('.cpSel').boundingClientRect(data => {
				data.forEach((item,index) =>{
					topArr.push(item.top)
				})
				this.topArr = topArr
			}).exec();
		}
	}
}
